import React from 'react';
import ReactDOM from 'react-dom';
import './index.css';
import App from './App';
import * as serviceWorker from './serviceWorker';
import {createStore} from 'redux';
import {Provider} from 'react-redux';


const initialState={
	jetPosition:{
		bottomDistance:'10vh',
		leftDistance:'50vw',
	},
	
	arrowPositions:{
		firstArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		secondArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		thirdArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		fourthArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		fifthArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		sixthArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		seventhArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		eighthArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
		ninthArrow:{
			bottomDistance:'10vh',
			leftDistance:'50vw',
			display:'none',
			initialized:'no',
		},
	}
	
	
};

function myReducer (state=initialState, action){
	if (action.type=='moveJet'){
		switch (action.payload) {
			case 'ArrowUp':
				return {
					...state,
					jetPosition:{
						bottomDistance:(parseInt(state.jetPosition.bottomDistance)+1)+'vh',
						leftDistance:state.jetPosition.leftDistance,
					}
				};
				
			case 'ArrowDown':
				return {
					...state,
					jetPosition:{
						bottomDistance:(parseInt(state.jetPosition.bottomDistance)-1)+'vh',
						leftDistance:state.jetPosition.leftDistance,
					}
				};
				
			case 'ArrowRight':
				return {
					...state,
					jetPosition:{
						bottomDistance:state.jetPosition.bottomDistance,
						leftDistance:(parseInt(state.jetPosition.leftDistance)+1)+'vw',
					}
				};
				
			case 'ArrowLeft':
				return {
					...state,
					jetPosition:{
						bottomDistance:state.jetPosition.bottomDistance,
						leftDistance:(parseInt(state.jetPosition.leftDistance)-1)+'vw',
					}
				};
				
			default:
				return state;
				
		} 
	}else if (action.type=='initializeArrow') {
		
		
		return {
			...state,
				arrowPositions: {
					...state.arrowPositions,
					[action.payload]:{
						...state.arrowPositions[action.payload],
							display:'block',
							initialized:'yes',
							leftDistance:state.jetPosition.leftDistance,
							bottomDistance:state.jetPosition.bottomDistance,
					}
				}
									
				
			
				
		}
	 }else if(action.type=='moveArrow'){
		 return {
			...state,
				arrowPositions: {
					...state.arrowPositions,
					[action.payload]:{
						...state.arrowPositions[action.payload],
							bottomDistance:parseInt(state.arrowPositions[action.payload].bottomDistance)+5+'vh',
					}
				}
		 }
	 }else {
		 return state;
	 }
	
	
}

const store=createStore(myReducer);

ReactDOM.render(<Provider store={store}><App /></Provider>, document.getElementById('root'));

// If you want your app to work offline and load faster, you can change
// unregister() to register() below. Note this comes with some pitfalls.
// Learn more about service workers: https://bit.ly/CRA-PWA
serviceWorker.unregister();
