import React from 'react';
import {connect} from 'react-redux';
import './App.css';
import jetplane from './jetplane.png';  
import arrow from './arrow.png';

class App extends React.Component {
	constructor(props) {
		super(props);
		
		
	}
	
	componentDidMount(){
		document.getElementById('jet-plane').style.left=this.props.jetPosition.leftDistance;
		document.getElementById('jet-plane').style.bottom=this.props.jetPosition.bottomDistance;
		document.addEventListener('keydown',this.pressFunction);
		setInterval(this.arrowMover,1000);
		setTimeout(this.printObject,10000);
	}
	
	printObject= () => {
		console.log(this.props.arrowPositions);
	}
	
	componentDidUpdate() {
		document.getElementById('jet-plane').style.left = this.props.jetPosition.leftDistance;
		document.getElementById('jet-plane').style.bottom = this.props.jetPosition.bottomDistance;
		
	}
	
	
	arrowMover = () => {
		for (var eachArrow in this.props.arrowPositions){
			if (this.props.arrowPositions[eachArrow].initialized=='no') {
				this.props.dispatch({type:'initializeArrow',payload:eachArrow});
				return;
			}else{
				this.props.dispatch({type:'moveArrow',payload:eachArrow});
				
			}
			
		}
	}
	
	
	pressFunction=(event) => {
		
		this.props.dispatch({type:'moveJet', payload:event.key});
		console.log(this.props.jetPosition.bottomDistance);
		console.log(this.props.jetPosition.leftDistance);
	};
	
	
	
	
	render() {
		let arrows=this.props.arrowPositions;
		let myStyle=[
		{display:arrows.firstArrow.display,left:arrows.firstArrow.leftDistance,bottom:arrows.firstArrow.bottomDistance,},
		{display:arrows.secondArrow.display,left:arrows.secondArrow.leftDistance,bottom:arrows.secondArrow.bottomDistance,},
		{display:arrows.thirdArrow.display,left:arrows.thirdArrow.leftDistance,bottom:arrows.thirdArrow.bottomDistance,},
		{display:arrows.fourthArrow.display,left:arrows.fourthArrow.leftDistance,bottom:arrows.fourthArrow.bottomDistance,},
		{display:arrows.fifthArrow.display,left:arrows.fifthArrow.leftDistance,bottom:arrows.fifthArrow.bottomDistance,},
		{display:arrows.sixthArrow.display,left:arrows.sixthArrow.leftDistance,bottom:arrows.sixthArrow.bottomDistance,},
		{display:arrows.seventhArrow.display,left:arrows.seventhArrow.leftDistance,bottom:arrows.seventhArrow.bottomDistance,},
		{display:arrows.eighthArrow.display,left:arrows.eighthArrow.leftDistance,bottom:arrows.eighthArrow.bottomDistance,},
		{display:arrows.ninthArrow.display,left:arrows.ninthArrow.leftDistance,bottom:arrows.ninthArrow.bottomDistance,},
		];
		return (
		
			<div id='main-canvas' >
				<img id='jet-plane' src={jetplane} alt='jet plane'/>
				<img 
					className='arrow' 
					id='arrow1' 
					src={arrow}
					style={myStyle[0]}
				/>
				<img 
					className='arrow' 
					id='arrow2' 
					src={arrow}
					style={myStyle[1]}
				/>
				<img 
					className='arrow' 
					id='arrow3' 
					src={arrow}
					style={myStyle[2]}
				/>
				<img 
					className='arrow' 
					id='arrow4' 
					src={arrow}
					style={myStyle[3]}
				/>
				<img 
					className='arrow' 
					id='arrow5' 
					src={arrow}
					style={myStyle[4]}
				/>
				<img 
					className='arrow' 
					id='arrow6' 
					src={arrow}
					style={myStyle[5]}
				/>
				<img 
					className='arrow' 
					id='arrow7' 
					src={arrow}
					style={myStyle[6]}
				/>
				<img 
					className='arrow' 
					id='arrow8' 
					src={arrow}
					style={myStyle[7]}
				/>
				<img 
					className='arrow' 
					id='arrow9' 
					src={arrow}
					style={myStyle[8]}
				/>
			</div>
		)
		
	}
	
}

function mapStateToProps(state){
	return {
			jetPosition:state.jetPosition,
			arrowPositions:state.arrowPositions,
	}
}
	

export default connect (mapStateToProps) (App);
